const yangiliklarReducer = (state = initialState, action = null) => {
    let stateCopy={...state}
    stateCopy.mainContaint=[...state.mainContaint]


switch (action.type) {
    case FOLLOW:{
        stateCopy.mainContaint[action.id]={...state.mainContaint[action.id]}
        stateCopy.mainContaint[action.id].isFollow=true
        return stateCopy
    }
    case UNFOLLOW:{
        stateCopy.mainContaint[action.id]={...state.mainContaint[action.id]}
        stateCopy.mainContaint[action.id].isFollow=false
        return stateCopy
    }
    default :return stateCopy


}

}
export const FollowActionCreater=(id)=>{
   return {
       type:FOLLOW,
       id:id
   }
}
export const UnFollowActionCreater=(id)=>{
   return {
       type:UNFOLLOW,
       id:id
   }
}

export default yangiliklarReducer